body {
    background-color: #3cff00;
    padding: 20px;
    margin: 0; 
  }
image {
    position: absolute;
    width: 300px;
    height: 300px;
    left: 32px;
    top: 264px;
    background: no-repeat top/50% url(./static/products/grass-patch-logo.png);
}

  h1, h2, p, ul, li {
    font-family: sans-serif;
  }
  ul.header li {
    display: inline;
    list-style-type: none;
    margin: 0;
  }
  ul.header {
    background-color: #111;
    padding: 0;
  }
  ul.header li a {
    color: #FFF;
    font-weight: bold;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
  }
  .content {
    background-color: #FFF;
    padding: 20px;
  }
  .content h2 {
    padding: 0;
    margin: 0;
  }
  .content li {
    margin-bottom: 10px;
  }
  .active {
      background-color: #0099FF;
    }
  
    
    